import React, {Component} from "react";
import '../App.css';
import MovieList from "./MovieList";
import Pagination from "./Pagination";
import MovieInfo from "./MovieInfo";
//import MovieFav from "./MovieFav";

class App extends Component {
  
  constructor(){
    super()
      this.state={
        movies: [],
        searchTerm: '',
        totalResults: 0,
        currentPage: 1,
        currentMovie: null,
        booksfav: [],
        isLoading: false,
    }
    this.apiKey="4e44d9029b1270a757cddc766a1bcb63"
    fetch('https://api.themoviedb.org/3/movie/popular?api_key=4e44d9029b1270a757cddc766a1bcb63&page=1').then(data => data.json()).then(data=> {      
      this.setState({movies: [...data.results], totalResults: data.total_results})
    })
  }  
  nextPage = (pageNumber) => {
    fetch(`https://api.themoviedb.org/3/movie/popular?api_key=4e44d9029b1270a757cddc766a1bcb63&page=${pageNumber}`).then(data => data.json()).then(data=> {
      this.setState({movies: [...data.results], currentPage: pageNumber})
    })
  }
    // append(id){
    //     booksfav.push(id)
    //     console.log(booksfav)
    // }

  viewMovieInfo = (id) => {
    const filteredMovie = this.state.movies.filter(movie => movie.id === id )
    const newCurrentMovie = filteredMovie.length > 0 ? filteredMovie[0] : null
    this.setState({currentMovie: newCurrentMovie})
  }
  closeMovieInfo = () => {
    this.setState({currentMovie: null})
  }

  // addToFavourite = (id) => {
  //   const filteredMovie = this.state.movies.filter(movie => movie.id === id)
  //   const newCurrentMovie = filteredMovie.length > 0 ? filteredMovie[0] : null
  //   this.setState({currentMovie: newCurrentMovie})
  // }

  // removeFromFavourite = (id) => {
  //   this.setState({currentMovie: null})
  // }

  render(){
    const numberPages = Math.floor(this.state.totalResults / 20);
  return (
    <>
    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
  <div className="container-fluid">
    <a className="navbar-brand" href="App.js">C I N E</a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav me-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <a className="nav-link active" aria-current="page" href="#">Favourites</a>
        </li>
      </ul>
    </div>
  </div>
</nav>
    <div className="App">
      {this.state.currentMovie == null ? <MovieList append = {this.append} viewMovieInfo = {this.viewMovieInfo} movies={this.state.movies}/> : <MovieInfo currentMovie={this.state.currentMovie} closeMovieInfo={this.closeMovieInfo}/>}
      {/* {this.state.currentMovie == null ? <MovieList viewMovieInfo = {this.viewMovieInfo} movies={this.state.movies}/> : <MovieFav currentMovie={this.state.currentMovie} addToFavourite={this.state.addToFavourite} removeFromFavourite={this.removeFromFavourite}/>} */}
      { this.state.totalResults > 20 && this.state.currentMovie == null ? <Pagination pages ={numberPages} nextPage={this.nextPage} currentPage={this.state.currentPage}/>:'' }
    </div>
    </>
  );
}
}
export default App;
