import React from 'react';

const Movie = (props) =>{
    return (
        <div className="movie">
            <div className="card">
                <div className="card-img-top">
                    {
                        props.image == null ? <img src="" alt="spiderman-movie" style={{width: "100%", height: 360}}/> : <img src={`https://image.tmdb.org/t/p/w1280${props.image}`} alt="spiderman-movie" style={{width: "100%", height: 360}}/>
                    }
                </div>
                <div class="card-body">
                    <a href="#" class="btn btn-primary" onClick={()=>props.viewMovieInfo(props.movieId)}>View Details</a>                    
                    {/* <a href="#" class="btn btn-primary" onClick={props.append(props.movieId)}>⭐</a>                                     */}
                </div>
            </div>
        </div>
    )
}
export default Movie;