import React, { useEffect, useState } from "react";

const MovieInfo = (props) => {
    const [videos, setVideos] = useState([]);
    const api = `https://api.themoviedb.org/3/movie/${props.currentMovie.id}/videos?api_key=4e44d9029b1270a757cddc766a1bcb63`;
    fetch(api).then(data => data.json()).then(data=> {
        setVideos(data.results[0]?.key)
    })
    return(        
        <div className="container">
            <div className="rowDesc" onClick={props.closeMovieInfo} style={{cursor: "pointer", paddingTop: 50, width: 130}}>
                <span style={{marginLeft: 10}}>⬅️Go Back</span>
            </div>                
            <div className="row-desc">
                <div className="col col-sm-11 col-md-4">
                {props.currentMovie.poster_path == null ? <img src={""}alt="movie-desc" style={{width: "100%", height: 400}}/> : <img src={`https://image.tmdb.org/t/p/w1280${props.currentMovie.poster_path}`} alt="movie-desc" style={{width: "100%", height: 400}}/>}
                </div>
                <div className="column">
                <div className="info-container">
                    <p className="heading">TITLE: </p>
                    <p>{props.currentMovie.title}</p>
                    <p className="heading">RELEASE DATE: </p>
                    <p>{props.currentMovie.release_date}</p>
                    <p className="heading">OVERVIEW:</p> 
                    <p>{props.currentMovie.overview}</p>
                    {/* <iframe width="560" height="315" src={`https://www.youtube.com/embed/${videos}`} frameborder="0" allow="autoplay; encrypted-media" allowfullscreen></iframe> */}
                    <a href={`https://www.youtube.com/watch?v=${videos}`} target ="_blank" className="btn btn-primary">View Trailer</a>
                </div>
            </div>
            </div>
            
        </div>
    )
}
export default MovieInfo;