import React from 'react';

const Pagination = (props) => {
    const pageLinks = []

    for(let i = 1; i <= props.pages + 1; i++){
        let active = props.currentPage === i ? 'active' : '';
        if(i>20){
            break;
        }
        pageLinks.push(<li className={`btn btn-info ${active}`} key={i} onClick={() => props.nextPage(i)}><a href="#">{i}</a></li>)
    }

    return (
        <div className="container-button">
            <div className="row-button">
                <ul className="pagination-button">
                    { props.currentPage > 1 ? <li className={`btn btn-info`}onClick={() => props.nextPage(props.currentPage - 1)}><a href="#">Prev</a></li>: ''}
                    { pageLinks }
                    { props.currentPage < props.pages + 1 ? <li className={`btn btn-info`}onClick={() => props.nextPage(props.currentPage + 1)}><a href="#">Next</a></li>: ''}
                </ul>
            </div>
        </div>
    )
}

export default Pagination;